.education {
  justify-content: space-around; }
  .education > div {
    padding: 50px;
    flex: 1; }
  .education > div:nth-child(1) {
    background-color: #4A525A; }
  .education > div:nth-child(2) {
    background-color: #fff; }
  .education .education--grid {
    flex-direction: column; }
    .education .education--grid > div {
      margin-bottom: 25px; }



