.menu {
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 3;
  background-color: #004BA8;
  line-height: 50px;
  height: 50px; }
  @media (min-width: 600px) {
    .menu {
      line-height: 100px;
      height: 100px; } }
  .menu .menu--desktop {
    display: none; }
    @media (min-width: 600px) {
      .menu .menu--desktop {
        display: block; } }
    .menu .menu--desktop ul {
      display: flex;
      justify-content: space-around; }
      .menu .menu--desktop ul li a {
        text-decoration: none;
        color: #fff;
        font-size: 1em;
        cursor: pointer; }
        .menu .menu--desktop ul li a:hover {
          color: #000; }
  .menu .menu--mobile {
    line-height: 50px;
    padding-left: 12.5px;
    color: #fff;
    display: block; }
    @media (min-width: 600px) {
      .menu .menu--mobile {
        line-height: 100px;
        display: none; } }
    .menu .menu--mobile .menu--mobile-show {
      position: fixed;
      text-align: center;
      box-sizing: border-box;
      padding: 50px;
      z-index: 4;
      top: 0;
      left: 0;
      background-color: #004BA8;
      width: 100%;
      height: 100%; }

.footer {
  margin: 50px;
  text-align: center; }



