.skills {
  justify-content: space-around; }
  .skills > div {
    padding: 50px;
    flex: 1; }
  .skills > div:nth-child(1) {
    background-color: #004BA8; }
  .skills > div:nth-child(2) {
    background-color: #fff; }
  .skills .skills--grid {
    flex-direction: column; }
    .skills .skills--grid > div {
      margin-bottom: 25px; }
  .skills .skills--progressContainer {
    background-color: #4A525A;
    width: 100%;
    height: 25px;
    position: relative;
    overflow: hidden; }
    .skills .skills--progressContainer span {
      height: 20px;
      text-align: right;
      background-color: #255C99;
      position: absolute; }



