.projects {
  justify-content: space-around; }
  .projects > div {
    padding: 50px;
    flex: 1; }
  .projects > div:nth-child(1) {
    background-color: #fff; }
  .projects > div:nth-child(2) {
    background-color: #255C99; }
  .projects .projects--grid {
    flex-direction: row; }
  .projects .flex-grid-images {
    flex-direction: column; }
    .projects .flex-grid-images > div {
      margin-bottom: 12.5px;
      position: relative; }
    .projects .flex-grid-images .projects--info {
      position: absolute;
      box-sizing: border-box;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      text-align: right;
      background-color: #4A525A;
      padding: 12.5px;
      z-index: 2; }
    .projects .flex-grid-images img {
      width: 100%;
      z-index: 1; }
      .projects .flex-grid-images img:hover {
        display: none;
        opacity: 0; }



