@import url(../../../node_modules/reset-css/reset.css);
@import url(../../../node_modules/animate.css/animate.min.css);
body, html {
  background-color: #fff;
  font-size: 18px;
  font-family: 'VT323', monospace; }
  @media (min-width: 600px) {
    body, html {
      font-size: 24px; } }

.mainWrapper {
  margin-top: 50px; }
  @media (min-width: 600px) {
    .mainWrapper {
      margin-top: 100px; } }

.flex-grid {
  display: flex;
  justify-content: center;
  align-items: stretch;
  flex-flow: row nowrap;
  flex-direction: column; }
  @media (min-width: 600px) {
    .flex-grid {
      flex-direction: row; } }

.flex-grid-reverse {
  flex-direction: column-reverse; }
  @media (min-width: 600px) {
    .flex-grid-reverse {
      flex-direction: row; } }

.flex-grid--title {
  display: flex;
  justify-content: center;
  align-items: center; }

.pull-right {
  float: right; }



