.me h2 {
  font-size: 1em;
  margin-top: 12.5px; }

.me > div {
  text-align: center;
  align-items: center; }

.me .me--profile img {
  max-width: 100px;
  border-radius: 50%;
  margin-top: 25px; }
  @media (min-width: 600px) {
    .me .me--profile img {
      margin-top: 50px; } }

.me ul {
  margin-top: 25px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap; }
  .me ul a, .me ul svg {
    color: #004BA8;
    font-size: 1.5em; }
  .me ul > li {
    flex: 1;
    padding: 12.5px 12.5px 0; }
    @media (min-width: 600px) {
      .me ul > li {
        padding: 25px 25px 0; } }
    .me ul > li:hover {
      animation: 1s swing; }
  .me ul:last-child {
    margin-bottom: 25px; }
    @media (min-width: 600px) {
      .me ul:last-child {
        margin-bottom: 50px; } }



